<template>
  <div class="todo" v-bind:style="rootStyles">
    <button class="theme" @click="(theme = !theme), changeRootColor">
      <div class="theme__type" :class="{ active: theme }">
        <span class="scroller" :class="{ change: theme }"></span>

        <transition name="light">
          <!-- <p class="theme__txt">{{ theme == true ? "light" : "dark" }}</p> -->
          <p class="theme__txt" v-show="theme">light</p>
        </transition>
        <transition name="dark">
          <p class="theme__txt" v-show="!theme">dark</p>
        </transition>
      </div>
    </button>
    <div class="todo__modal">
      <h2 class="todo__modal-title">To Do List</h2>

      <form
        action=""
        class="todo__modal-form"
        @submit.prevent="$emit('addTask')"
      >
        <input
          type="text"
          name=""
          id=""
          placeholder="New Task"
          class="todo__modal-inp"
          required
          @input="$emit('taskName', $event.target.value)"
          :value="inpValue"
        />
        <button class="todo__modal-add-btn btn">
          <span class="plus"></span>
        </button>
      </form>

      <ul class="todo__list" :class="{ none: tasks == 0 ? true : false }">
        <Transition name="none">
          <li class="tasks__none" v-show="!tasks.length">no tasks</li>
        </Transition>

        <TransitionGroup name="task">
          <Task
            v-for="(task, idx) in tasks"
            :key="idx"
            :task="task"
            @removeTask="$emit('removeTask', task.id)"
            @sendInf="$emit('sendInf', task.completed, task.id)"
          />
        </TransitionGroup>
      </ul>

      <div class="clear__btns">
        <a
          href="#!"
          class="clear__compleated btn"
          @click="$emit('deleteCompleted')"
          >Clear completed</a
        >
        <a href="#!" class="clear__all btn" @click="$emit('deleteAll')"
          >Clear all</a
        >
      </div>

      <p class="todo__tasks-num">Pending Tasks: {{ pendings }}</p>
    </div>
  </div>
</template>

<script>
import Task from "../Tasks/Tasks";

export default {
  name: "List",
  components: {
    Task,
  },
  data() {
    return {
      theme: false,
      primaryColor: "#1e1e22",
      secondaryColor: "#303030",
      thirdColor: "#ccc",
      listColor: "#d5d5d5",
    };
  },
  props: {
    tasks: {
      type: Array,
      default: [],
    },
    inpValue: {
      type: String,
      default: "",
    },
  },
  computed: {
    pendings() {
      return this.tasks.filter((item) => item.completed == false).length;
    },
    rootStyles() {
      return {
        "--primary-color": this.primaryColor,
        "--secondary-color": this.secondaryColor,
        "--third-color": this.thirdColor,
        "--list-color": this.listColor,
      };
    },
    changeRootColor() {
      if (this.theme == true) {
        (this.primaryColor = "#46bbc5"),
          (this.secondaryColor = "#303030"),
          (this.thirdColor = "#e9e9e9");
        this.listColor = "#fff";
      } else {
        (this.primaryColor = "#1e1e22"),
          (this.secondaryColor = "#303030"),
          (this.thirdColor = "#ccc");
        this.listColor = "#d5d5d5";
      }
    },
  },
};
</script>
