<template>
  <List
    @taskName="inpTask = $event"
    :tasks="tasks"
    @addTask="addTask"
    @deleteAll="deleteAll"
    @deleteCompleted="deleteCompleted"
    @removeTask="removeTask"
    :inpValue="inpText"
  />
</template>

<script>
import List from "./components/List/List.vue";

export default {
  name: "App",
  components: {
    List,
  },
  data() {
    return {
      inpTask: "",
      inpText: "",
      tasks: [],
    };
  },
  methods: {
    addTask() {
      let pushObj = {
        id: new Date().getTime(),
        task: this.inpTask,
        completed: false,
      };

      this.tasks.push(pushObj);
    },

    deleteAll() {
      this.tasks = [];
    },

    removeTask(id) {
      let findId = this.tasks.findIndex((item) => item.id == id);
      this.tasks.splice(findId, 1);
    },

    deleteCompleted(completed, id) {
      this.tasks = this.tasks.filter((task) => !task.completed);
    },
  },
};
</script>

