<template>
  <li class="todo__item task">
    <p class="task__txt" :class="{ line: task.completed }" id="txt"  @click="task.completed = !task.completed, $emit('sendInf', task.completed, task.id )">
      {{ task.task }}
    </p>
    <a href="#!" class="task__delete" @click="$emit('removeTask', task.id)">
      <img src="@/assets/img/icon/delete.svg" alt="" />
    </a>
  </li>
</template>

<script>
export default {
  name: "Tasks",
  props: {
    task: {
      type: Object,
      required: true,
    },
  },
};
</script>
